'use client'

import { useState, useEffect } from 'react'

interface Member {
  id: number
  first_name: string
  last_name: string
  role: string
  email: string
  phone: string
  photo?: string
}

const roleLabels: Record<string, string> = {
  PASTOR: 'Pasteur',
  ELDER: 'Ancien',
  DEACON: 'Diacre',
  MEMBER: 'Membre',
  VISITOR: 'Visiteur',
}

const roleColors: Record<string, string> = {
  PASTOR: 'bg-purple-100 text-purple-800 border-purple-200',
  ELDER: 'bg-blue-100 text-blue-800 border-blue-200',
  DEACON: 'bg-green-100 text-green-800 border-green-200',
  MEMBER: 'bg-gray-100 text-gray-800 border-gray-200',
  VISITOR: 'bg-yellow-100 text-yellow-800 border-yellow-200',
}

export default function MembersPage() {
  const [members, setMembers] = useState<Member[]>([])
  const [loading, setLoading] = useState(true)
  const [search, setSearch] = useState('')
  const [roleFilter, setRoleFilter] = useState('ALL')

  useEffect(() => {
    // D'abord essayer l'API Django
    fetch('http://localhost:8000/api/members/')
      .then(res => {
        if (!res.ok) throw new Error('API non disponible')
        return res.json()
      })
      .then(data => {
        setMembers(data)
        setLoading(false)
      })
      .catch(() => {
        // Données de démonstration si l'API n'est pas disponible
        const demoMembers: Member[] = [
          { id: 1, first_name: 'Jean', last_name: 'Martin', role: 'PASTOR', email: 'pasteur@eglise.tg', phone: '+228 90 12 34 56' },
          { id: 2, first_name: 'Marie', last_name: 'Dupont', role: 'ELDER', email: 'marie@eglise.tg', phone: '+228 91 23 45 67' },
          { id: 3, first_name: 'Paul', last_name: 'Dubois', role: 'DEACON', email: 'paul@eglise.tg', phone: '+228 92 34 56 78' },
          { id: 4, first_name: 'Sophie', last_name: 'Lambert', role: 'MEMBER', email: 'sophie@eglise.tg', phone: '+228 93 45 67 89' },
          { id: 5, first_name: 'Thomas', last_name: 'Moreau', role: 'MEMBER', email: 'thomas@eglise.tg', phone: '+228 94 56 78 90' },
          { id: 6, first_name: 'Julie', last_name: 'Bernard', role: 'VISITOR', email: 'julie@eglise.tg', phone: '+228 95 67 89 01' },
        ]
        setMembers(demoMembers)
        setLoading(false)
      })
  }, [])

  const filteredMembers = members.filter(member => {
    const matchesSearch = search === '' || 
      member.first_name.toLowerCase().includes(search.toLowerCase()) ||
      member.last_name.toLowerCase().includes(search.toLowerCase()) ||
      member.email.toLowerCase().includes(search.toLowerCase())
    
    const matchesRole = roleFilter === 'ALL' || member.role === roleFilter
    
    return matchesSearch && matchesRole
  })

  const roleCounts = members.reduce((acc, member) => {
    acc[member.role] = (acc[member.role] || 0) + 1
    return acc
  }, {} as Record<string, number>)

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="max-w-7xl mx-auto px-4">
        {/* Header */}
        <header className="mb-12">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">
            Annuaire des Membres
          </h1>
          <p className="text-lg text-gray-600 mb-8">
            Rencontrez notre communauté de {members.length} membres
          </p>
          
          {/* Stats */}
          <div className="grid grid-cols-2 md:grid-cols-5 gap-4 mb-8">
            {['PASTOR', 'ELDER', 'DEACON', 'MEMBER', 'VISITOR'].map(role => (
              <div key={role} className="bg-white p-4 rounded-xl shadow text-center">
                <div className={`inline-block px-3 py-1 rounded-full text-sm font-semibold ${roleColors[role]} mb-2`}>
                  {roleLabels[role]}
                </div>
                <div className="text-2xl font-bold">{roleCounts[role] || 0}</div>
              </div>
            ))}
          </div>
          
          {/* Filtres */}
          <div className="bg-white p-6 rounded-xl shadow-lg mb-8">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label htmlFor="member-search" className="block text-sm font-medium text-gray-700 mb-2">
                  Rechercher un membre
                </label>
                <input
                  id="member-search"
                  type="text"
                  placeholder="Nom, prénom ou email..."
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  aria-label="Rechercher un membre par nom, prénom ou email"
                />
              </div>
              
              <div>
                <label htmlFor="role-filter" className="block text-sm font-medium text-gray-700 mb-2">
                  Filtrer par rôle
                </label>
                <select 
                  id="role-filter"
                  value={roleFilter}
                  onChange={(e) => setRoleFilter(e.target.value)}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  aria-label="Filtrer les membres par rôle"
                  title="Sélectionnez un rôle pour filtrer les membres"
                >
                  <option value="ALL">Tous les rôles</option>
                  <option value="PASTOR">Pasteurs</option>
                  <option value="ELDER">Anciens</option>
                  <option value="DEACON">Diacres</option>
                  <option value="MEMBER">Membres</option>
                  <option value="VISITOR">Visiteurs</option>
                </select>
              </div>
            </div>
          </div>
        </header>

        {/* Liste des membres */}
        <div className="bg-white rounded-xl shadow-lg overflow-hidden">
          <div className="px-6 py-4 border-b border-gray-200">
            <h2 className="text-2xl font-bold text-gray-800">
              Liste des Membres ({filteredMembers.length})
            </h2>
          </div>

          {loading ? (
            <div className="p-12 text-center">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto" 
                   role="status"
                   aria-label="Chargement des membres en cours">
                <span className="sr-only">Chargement...</span>
              </div>
              <p className="mt-4 text-gray-600">Chargement des membres...</p>
            </div>
          ) : filteredMembers.length === 0 ? (
            <div className="p-12 text-center">
              <p className="text-gray-600">Aucun membre ne correspond à votre recherche.</p>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full">
                <caption className="sr-only">Liste des membres de l'église</caption>
                <thead className="bg-gray-50">
                  <tr>
                    <th scope="col" className="px-6 py-3 text-left text-sm font-semibold text-gray-700">Membre</th>
                    <th scope="col" className="px-6 py-3 text-left text-sm font-semibold text-gray-700">Rôle</th>
                    <th scope="col" className="px-6 py-3 text-left text-sm font-semibold text-gray-700">Contact</th>
                    <th scope="col" className="px-6 py-3 text-left text-sm font-semibold text-gray-700">Actions</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {filteredMembers.map((member) => (
                    <tr key={member.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4">
                        <div className="flex items-center">
                          <div className="flex-shrink-0 h-12 w-12 bg-blue-100 rounded-full flex items-center justify-center text-blue-600 font-bold"
                               aria-hidden="true">
                            {member.first_name[0]}{member.last_name[0]}
                          </div>
                          <div className="ml-4">
                            <div className="font-medium text-gray-900">
                              {member.first_name} {member.last_name}
                            </div>
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4">
                        <span className={`px-3 py-1 rounded-full text-sm font-semibold border ${roleColors[member.role]}`}
                              aria-label={`Rôle: ${roleLabels[member.role]}`}>
                          {roleLabels[member.role]}
                        </span>
                      </td>
                      <td className="px-6 py-4">
                        <div className="text-gray-900 font-medium">{member.email}</div>
                        <div className="text-gray-600 text-sm">{member.phone || 'Non renseigné'}</div>
                      </td>
                      <td className="px-6 py-4">
                        <button 
                          className="text-blue-600 hover:text-blue-800 font-medium"
                          onClick={() => console.log('Voir profil:', member.id)}
                          aria-label={`Voir le profil de ${member.first_name} ${member.last_name}`}
                        >
                          Voir profil
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>

        {/* Information pour les administrateurs */}
        <div className="mt-8 p-6 bg-blue-50 rounded-xl">
          <div className="flex justify-between items-center">
            <div>
              <h3 className="text-lg font-semibold text-blue-800 mb-2">Administration</h3>
              <p className="text-blue-700">
                Pour gérer les membres, utilisez le panneau d'administration Django.
              </p>
            </div>
            <a 
              href="http://localhost:8000/admin" 
              target="_blank"
              rel="noopener noreferrer"
              className="bg-blue-600 text-white px-6 py-2 rounded-lg font-semibold hover:bg-blue-700 transition"
              aria-label="Accéder au panneau d'administration Django"
            >
              Accéder à l'admin
            </a>
          </div>
        </div>
      </div>
    </div>
  )
}